.overlay {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  pointer-events: none;
  transition: opacity .25s $ease;
  &.is-active {
    opacity: 1;
    pointer-events: all; }
  &__bg {
    position: fixed 0;
    z-index: 99;
    background: rgba(black, .5); }
  &__content {
    position: relative;
    z-index: 100;
    top: 200px;
 }    // to be defined in js
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    background: $colorPrimary;
    border-radius: 10px;
    color: white;
    padding: 60px 36px 32px;
    &--search {
      flex-direction: row;
      max-width: 900px;
      padding: 24px;
      margin-left: auto;
      height: auto;
      .overlay__close {
        top: 27px; } } }
  &__scroller {
    flex: 1 1 auto;
    min-height: 0;
    max-height: 100vh;
    overflow-y: auto; }
  &__close {
    position: absolute;
    z-index: 3;
    top: 20px;
    right: 20px;
    padding: 10px;
    font-size: 0;
    cursor: pointer;
    transition: color .25s $ease;
    .ico {
      font-size: 24px; }
    &:hover {
      color: rgba(white, .7); } }
  &__search {
    display: flex;
    width: 100%;
    padding-right: 56px;
    input {
      -webkit-appearance: none;
      flex: 1 1 auto;
      display: block;
      border: 0;
      box-shadow: none;
      font-size: 14px;
      line-height: 100%;
      color: #A2A2A2;
      padding: 15px 14px;
      background: white;
      margin-right: 10px;
      border: 2px solid transparent;
      transition: border .25s $ease;
      &:focus, &:active {
        background: white;
        border-color: $colorBlue; } }
    button {
      flex: 0 0 auto; } }
  &__cities {
    .col {
      display: flex;
      flex-direction: column;
      align-items: flex-start; }
    a {
      display: inline-block;
      padding: 3px 0;
      font-weight: 500;
      transition: color .25s $ease;
      &:hover {
        color: rgba(white, .5); } } } }


@include r($md) {
  .overlay {
    &__wrapper {
      padding: 40px 20px 32px; } } }
